import { faArrowLeft, faCheck, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, parseUrlQuery, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';


class AdminMemberBenefits extends Component {
  static contextType = AppContext;

  state = {
    isMember: false,
    member: 'Members',
    mybenefits: [],
    pageIsLoading: true
  }

  getAllBenefits = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/all-benefits', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({ mybenefits: resp.data });
      }
    } catch (error) {
      console.log(error);
    }
  }

  getMemberBenefits = async (email) => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('email', email);
      let resp = await request('/api/admin/member-benefits', 'POST', body, headers);

      if (resp.status === true) {
        this.setState({ mybenefits: resp.data });
      }
    } catch (error) {
      console.log(error);
    }
  }

  giveBenefit = async (id, userId) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('userId', userId)
      body.append('benefitId', id)
      let resp = await request('/api/admin/givebenefit', 'POST', body, headers);

      if (resp.status === true) {
        alert('Given successfully', 'success');
        this.state.isMember === false ? this.getAllBenefits() : this.getMemberBenefits(this.state.member);
      } else {
        alert('Not given', 'error');
      }
    } catch (error) {
      alert('Network Error')
      console.log(error);
    }
  }

  cancelBenefit = async (id, userId) => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('userId', userId)
      body.append('benefitId', id)
      let resp = await request('/api/admin/cancelbenefit', 'POST', body, headers);

      if (resp.status === true) {
        alert('Cancelled successfully', 'success');
        this.state.isMember === false ? this.getAllBenefits() : this.getMemberBenefits(this.state.member);
      } else {
        alert('Not cancelled', 'error');
      }
    } catch (error) {
      alert('Network Error')
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin'], 'member benefits');
    let query = parseUrlQuery();
    if (query.email !== undefined) {
      this.setState({ isMember: true, member: query.email });
      this.getMemberBenefits(query.email)
    } else await this.getAllBenefits();
    this.setState({ pageIsLoading: false })
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/membersbenefits'>
          <section className='container my-3 px-3 text-secondary'>

            <div className='mb-2 text-dark'>
              <h1 className='lh-0 my-0'>Members Benefits</h1>
              {this.state.mybenefits.length > 0 && <span>{this.state.member} have accrued {this.state.mybenefits.length} benefits</span>}
              <hr className='my-0' />
              {
                this.state.isMember === true &&
                <button className='btn btn-sm btn-muted' onClick={() => { this.props.history.goBack() }}>
                  <FontAwesomeIcon icon={faArrowLeft}></FontAwesomeIcon><span className='ms-1'>Back</span>
                </button>
              }
            </div>

            {
              this.state.pageIsLoading === true ?
                <div id="loadingDiv">
                  <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                    <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                    <span className='ms-2 align-middle'>loading...</span>
                  </div>
                </div>
                :
                <div>

                  <div>
                    {
                      this.state.mybenefits.length === 0 &&
                      <div className='my-5 py-5 d-flex flex-column align-items-center'>
                        <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  No Accrued Benefits
                </div>
                    }
                  </div>

                  <div className='text-secondary mt-4'>
                    {
                      this.state.mybenefits.map((benefit, index) => (

                        <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                          <div className='d-flex justify-content-between align-items-top'>
                            <div className='fw-bold lh-0 text-primary'>
                              {benefit.achievement} <br />
                              <small>{benefit.email}</small>
                            </div>
                            <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                          </div>

                          <div className='fs-6 lh-0 mt-3'>
                            {
                              benefit.benefit === null ? "No souvenir" : benefit.benefit
                            }
                          </div>

                          <div className='d-flex justify-content-between align-items-center'>
                            <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                            <div>
                              {
                                benefit.status === 'pending' &&
                                <>
                                  <button className='btn btn-sm pt-0 pb-1 btn-danger' title="Mark this benefit as cancelled" onClick={() => this.cancelBenefit(benefit.id, benefit.user_id)}>cancel</button>
                                  <button className='ms-1 btn btn-sm pt-0 pb-1 btn-primary' title="Mark this benefit as given" onClick={() => this.giveBenefit(benefit.id, benefit.user_id)}>given</button>
                                </>
                              }
                              {
                                benefit.status === 'given' &&
                                <>
                                  <div className='text-success' style={{ fontSize: '14px' }}>
                                    given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon> <br />
                                    {formatDate(benefit.updated_at)}
                                  </div>
                                </>
                              }
                              {
                                benefit.status === 'cancelled' &&
                                <>
                                  <div className='text-danger' style={{ fontSize: '14px' }}>
                                    cancelled <br />
                                    {formatDate(benefit.updated_at)}
                                  </div>
                                </>
                              }
                            </div>

                          </div>
                        </div>

                      ))
                    }
                  </div>

                </div>
            }

          </section>
        </Layout>
      </>
    );
  }

}

export default AdminMemberBenefits;