import React, { Component } from 'react';
import { usePaystackPayment } from 'react-paystack';
// import logo from '../../assets/imgs/badge.png';
import paystack from '../../assets/imgs/paystack.png';
import flutterwave from '../../assets/imgs/flutterwave.png';
import { AppContext } from '../../context/AppContext';
import { alert, formatDate, formatMoney, query, request } from '../../common/Utils';
import Toolbar from '../toolbar/Toolbar';


let token = '';
let role = '';

// you can call this function anything
function onPaystackSuccess(reference) {
  // Implementation for whatever you want to do with reference and after success call.
  verifyPayment(reference.trxref, token);
};

// you can call this function anything
function onPaystackClose() {
  // implementation for  whatever you want to do when the Paystack dialog closed.
  console.log('closed')
  query("#loadingDiv")[0].classList.add('d-none');
}

async function initiatePayment(reference, token, invoice, service) {
  let body = new FormData();
  body.append('reference', reference);
  body.append('invoicenumber', invoice);
  body.append('service', service);

  let headers = new Headers();
  headers.append('AuthToken', token);

  try {
    let resp = await request('/api/payment/initiate', 'POST', body, headers);
    if (resp.status === true) {
      return true;
    } else {
      return false;
    }
  } catch (error) {
    alert('Network Error', 'error');
    return false;
  }
}

async function verifyPayment(reference, token) {
  alert('verifying payment...', 'info', false);

  let body = new FormData();
  body.append('reference', reference);

  let headers = new Headers();
  headers.append('AuthToken', token);

  try {
    let resp = await request('/api/payment/verify', 'POST', body, headers);
    if (resp.status === true) {
      alert(resp.message, 'success');
      query("#loadingDiv")[0].classList.add('d-none');
      // log the user out
      localStorage.clear();
      setTimeout(() => {
        window.location.href = "/login";
      }, 2000);
    } else {
      query("#loadingDiv")[0].classList.add('d-none');
      alert(resp.message, 'error');
      return false;
    }
  } catch (error) {
    query("#loadingDiv")[0].classList.add('d-none');
    alert('Network Error', 'error');
    return false;
  }
}

function PayWithPaystack(props) {
  let { email, invoice, amount, authtoken, authrole } = { ...props };
  token = authtoken; role = authrole;
  let reference = `${invoice}-${Math.floor(Math.random() * 99999)}`;

  let config = {
    reference: reference,
    email: email.toString(),
    amount: amount * 100, // convert the naira to kobo
    publicKey: 'pk_test_579a731077fa2885e1119ad97a783bca3c66fb8d',
  };

  const initializePayment = usePaystackPayment(config);
  return (
    <div>
      <button className='btn btn-muted w-100 h-100 border mt-1' title="Pay with Paystack" disabled={false} onClick={async (e) => {
        try {
          console.log('Button Clicked...', e.target)
          query("#loadingDiv")[0].classList.remove('d-none');
          e.target.disabled = true;
          // return;
          let initiate = await initiatePayment(reference, token, invoice, 'Paystack');
          if (initiate === true) {
            await initializePayment(onPaystackSuccess, onPaystackClose);
          } else {
            query("#loadingDiv")[0].classList.add('d-none');
            alert('Could not initate Paystack service.', 'error');
          }
        } catch (error) {
          console.log(error);
          query("#loadingDiv")[0].classList.add('d-none');
          alert('Could not initate Paystack service.', 'error');
        }
      }}>
        <img src={paystack} className='img' width={200} alt="Pay with Paystack" />
      </button>
    </div>
  );
};

