class PayInvoice extends Component {
  static contextType = AppContext;

  state = {
    invoice: {},
    noUnpaidInvoice: false,
    pageIsLoading: true
  }

  getUnpaidInvoice = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let resp = await request('/api/invoice/unpaid', 'GET', null, headers);
      if (resp.status === true) {
        this.setState({
          invoice: resp.data
        });
      } else {
        this.setState({ noUnpaidInvoice: true })
      }
    } catch (error) {
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['member', 'admin']);
    await Promise.all([
      this.getUnpaidInvoice()
    ]);
    this.setState({ pageIsLoading: false })
  }

  render() {
    return (
      <>
        <Toolbar props={this.props}></Toolbar>
        <section className='container my-3 px-3 text-secondary'>

          <div className='mb-3 text-dark'>
            <h1>Payment Invoice</h1>
            <hr className='my-0' />
          </div>
          {
            this.state.pageIsLoading === true ?
              <div id="loadingDiv">
                <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                  <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                  <span className='ms-2 align-middle'>loading...</span>
                </div>
              </div>
              :
              <div>
                <div className='mb-3 rounded border border-light shadow px-lg-4 px-3 pt-lg-5 pt-3 pb-5'>

                  {
                    this.state.noUnpaidInvoice === true ?
                      <>
                        <div className='d-flex justify-content-center'>
                          <div className='fs-3'>
                            No Unpaid Invoice
                      </div>
                        </div>
                      </>
                      :
                      <>
                        <div className='d-flex justify-content-between align-items-center'>
                          <div className='fs-2 text-primary'>Invoice #{this.state.invoice.invoice_number}</div>
                          <div>
                            {formatDate(this.state.invoice.created_at)} <br />
                            <div style={{ fontSize: '14px' }} className={this.state.invoice.status === 'unpaid' ? 'text-danger' : ''}>{this.state.invoice.status}</div>
                          </div>
                        </div>
                        <div>
                          <div>
                            <div className='fs-6'>{this.state.invoice.invoice_description}</div>
                            <div className='fs-6 mt-1 lh-0'>
                              Total:<span className='ms-1 fs-2'>{formatMoney(this.state.invoice.amount_due, 0)}</span>
                            </div>
                          </div>
                        </div>

                        <div className='mt-lg-3'>
                          <hr />
                          <span>Complete payment with:</span>

                          <div className='row mt-2'>
                            <div className='col-lg-6 col-md-6 col-12 mb-3'>
                              <PayWithPaystack email={this.context.auth.user.email} invoice={this.state.invoice.invoice_number} amount={this.state.invoice.amount_due} authtoken={this.context.auth.token} role={this.context.auth.role} />
                            </div>

                            <div className='col-lg-6 col-md-6 col-12 mb-3'>
                              <PayWithFlutterwave />
                            </div>

                          </div>

                        </div>

                        <div id="loadingDiv" className='d-none'>
                          <div style={{ zIndex: 9999, backgroundColor: 'rgba(0,0,0,.50)' }} className="position-absolute h-100 vw-100 top-0 start-0 d-flex justify-content-center align-items-center text-light">
                            <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                            <span className='ms-2 align-middle'>loading...</span>
                          </div>
                        </div>

                      </>
                  }

                </div>
              </div>
          }
        </section>
      </>
    );
  }
}

export default PayInvoice;
