import { faCheck, faExclamation } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { alert, formatDate, formatMoney, query, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../../members/layout/Layout';


class AdminInvoices extends Component {
  static contextType = AppContext;

  state = {
    invoices: [],
    limit: 10,
    page: 1,
    view: [],
    searchInvoices: [],
    pageIsLoading: true
  }

  getAllInvoices = async () => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let resp = await request('/api/admin/get-invoices', 'GET', null, headers);

      if (resp.status === true) {
        this.setState({
          invoices: resp.data
        });
        this.setState({ view: this.state.invoices });
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  queryInvoice = async (userId) => {
    // get my benefits
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);
      let body = new FormData();
      body.append('userId', userId);
      let resp = await request('/api/queryinvoice', 'POST', body, headers);

      if (resp.status === true) {
        alert('Queried successfully', 'success');
      }
    } catch (error) {
      // alert('Error retrieving benefits')
      console.log(error);
    }
  }

  handleInvoiceSearch = (e) => {
    e.preventDefault();
    e.stopPropagation();

    let search = query('#memberSearchbox')[0];
    let searchinvoices = this.state.invoices.filter((invoice) => {

      if (invoice.email === search.value) return invoice;

      if (invoice.invoice_number === search.value) return invoice;

      return false;

    });

    this.setState({ view: searchinvoices, searchInvoices: searchinvoices || this.state.invoices });
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['admin'], 'invoices');
    await this.getAllInvoices();
    this.setState({ pageIsLoading: false })
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/invoices'>
          <section className='container my-3 px-3'>

            <div className='mb-3'>
              <h1 className='lh-0 my-0'>Invoices</h1>
              <span>There are a total of {this.state.invoices.length} invoices</span>
              <hr className='my-0' />
            </div>

            {
              this.state.pageIsLoading === true ?
                <div id="loadingDiv">
                  <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                    <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                    <span className='ms-2 align-middle'>loading...</span>
                  </div>
                </div>
                :
                <div>

                  <div>
                    {
                      this.state.invoices.length === 0 &&
                      <div className='my-5 py-5 d-flex flex-column align-items-center'>
                        <FontAwesomeIcon icon={faExclamation} size='2x'></FontAwesomeIcon> <br />
                  There are no Invoices
                </div>
                    }
                  </div>

                  {this.state.invoices.length > 0 &&
                    <>

                      <div className='w-100'>
                        <form onSubmit={e => e.preventDefault()}>
                          <input type="search" id="memberSearchbox" onChange={this.handleInvoiceSearch} className='form-control form-control w-100' placeholder='search with email or invoice number' />
                          <small>{this.state.searchInvoices.length} results found</small>
                        </form>
                      </div>

                      <div className='text-secondary mt-4'>
                        {
                          this.state.view.slice(((this.state.page * this.state.limit) - this.state.limit), (this.state.page * this.state.limit)).map((invoice, index) => (

                            <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                              <div className='d-flex justify-content-between align-items-top'>
                                <div className='fw-bold lh-0 text-primary'>#{invoice.invoice_number}</div>
                                <div style={{ fontSize: '12px' }}>{formatDate(invoice.created_at)}</div>
                              </div>

                              <div className='fs-6 lh-0 mt-3'>
                                {invoice.invoice_description === null ? "No description" : invoice.invoice_description} <br />
                                {invoice.email}
                              </div>

                              <div className='d-flex justify-content-between '>
                                <div>
                                  amount&nbsp;due:&nbsp;<span className='fs-5'>{formatMoney(invoice.amount_due, 0)}</span> <br />
                            amount&nbsp;paid:&nbsp;<span className='fs-5'>{formatMoney(invoice.amount_paid, 0)}</span>
                                </div>
                                <div>
                                  {
                                    invoice.status === 'unpaid' &&
                                    <button className='btn btn-sm pt-0 pb-1 btn-primary' onClick={() => this.queryInvoice(invoice.user_id)}>query</button>
                                  }
                                  {
                                    invoice.status === 'paid' &&
                                    <>
                                      <div className='text-success' style={{ fontSize: '14px' }}>
                                        paid <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon>
                                      </div>
                                    </>
                                  }
                                </div>

                              </div>
                            </div>

                          ))
                        }
                      </div>

                      <div className='my-4 d-flex justify-content-between align-items-center'>

                        <button disabled={this.state.page === 1} className='btn btn-sm btn-primary' onClick={() => { this.setState({ page: this.state.page - 1 }) }}>&laquo; Previous</button>

                        <span>Page {this.state.page} / {Math.ceil(this.state.view.length / this.state.limit)}</span>

                        <button disabled={(((this.state.page + 1) * this.state.limit) - this.state.limit) > this.state.view.length} className='btn btn-sm btn-primary px-4' onClick={() => { this.setState({ page: this.state.page + 1 }) }}>Next &raquo;</button>

                      </div>

                    </>
                  }

                </div>
            }

          </section>
        </Layout>
      </>
    );
  }

}

export default AdminInvoices;