import { faArrowRight, faCheck } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { formatDate, formatMoney, request } from '../../common/Utils';
import { AppContext } from '../../context/AppContext';
import Layout from '../layout/Layout';

class MemberDashboard extends Component {
  static contextType = AppContext;

  state = {
    userPackage: {},
    activeSlot: {},
    recentUnreadNotifications: [],
    totalCashBenefitAccrued: 0,
    recentPendingBenefits: [],
    pageIsLoading: true
  }

  getDashboard = async () => {
    try {
      let headers = new Headers();
      headers.append('AuthToken', this.context.auth.token);

      let resp = await request('/api/dashboard', 'GET', null, headers);
      if (resp.status === true) {
        let dashboard = resp.data;
        this.setState({
          userPackage: dashboard.userPackage ?? {},
          activeSlot: dashboard.activeSlot ?? {},
          recentUnreadNotifications: dashboard.recentUnreadNotifications,
          totalCashBenefitAccrued: dashboard.totalCashBenefitAccrued,
          recentPendingBenefits: dashboard.recentPendingBenefits
        });
      }
    } catch (error) {
      console.log(error);
    }
  }

  async componentDidMount() {
    await this.context.auth.AuthGuard(['member', 'admin']);
    await Promise.all([
      this.getDashboard()
    ]);
    this.setState({ pageIsLoading: false })
  }

  render() {
    return (
      <>
        <Layout props={this.props} page='/dashboard'>
          <section className='container my-3 px-3 text-secondary'>

            <div className='mb-3 text-dark'>
              <h1>Dashboard</h1>
              <hr className='my-0' />
            </div>

            {
              this.state.pageIsLoading === true ?
                <div id="loadingDiv">
                  <div className="h-100 d-flex justify-content-center align-items-center text-secondary">
                    <span className="spinner-border spinner-border border-2 spinner-border-sm" role="status" aria-hidden="true"></span>
                    <span className='ms-2 align-middle'>loading...</span>
                  </div>
                </div>
                :
                <div>

                  {/* Package and Slots */}
                  <div className='container'>
                    <div className='row'>

                      <div className='col-lg-7 col-12'>
                        <div className='mb-3 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>

                          <div className='d-flex justify-content-between'>
                            <div className='fw-bold text-primary'>Your Package</div>
                          </div>

                          <div className='my-4'>

                            <h2 className='lh-0 my-0'>{this.state.userPackage.slot_program} Package</h2>
                            <span>You have {this.state.userPackage.no_slots} slots</span>

                          </div>

                          <div className='d-flex justify-content-between '>
                            <div className='text-primary' style={{ fontSize: '14px' }}>Current Level: {this.state.userPackage.rank}</div>
                            <div style={{ fontSize: '14px' }}>{this.state.userPackage.status === 'pending' ? <Link to={this.context.auth.role === 'admin' ? '/admin/invoice' : '/member/invoice'} className='btn btn-primary btn-sm m-0 py-0 px-2'>Activate</Link> : <span>{this.state.userPackage.status}</span>}</div>
                          </div>
                        </div>
                      </div>

                      <div className='col-lg-5 col-12'>
                        <div className='mb-3 rounded border border-light shadow-sm px-lg-4 px-3 pt-3 pb-0'>

                          <div className='d-flex justify-content-between align-items-top'>
                            <div className='fw-bold text-primary'>Active Slot</div>
                            <div className='' style={{ fontSize: '14px' }}>
                              {this.state.activeSlot.referrals_acquired}/{this.state.activeSlot.referrals_required}
                            </div>
                          </div>

                          <div className='my-5'>

                            <div className="progress">
                              <div className={`progress-bar progress-bar-striped ${this.state.activeSlot.status === 'pending' ? 'bg-secondary' : 'bg-primary'} ${this.state.activeSlot.status === 'active' && 'progress-bar-animated'}`} role="progressbar" style={{ width: `${Math.ceil(((this.state.activeSlot.referrals_acquired) / this.state.activeSlot.referrals_required) * 100)}%` }} >{Math.floor((this.state.activeSlot.referrals_acquired / this.state.activeSlot.referrals_required) * 100)}%</div>
                            </div>

                            <div className='d-flex justify-content-between '>
                              <div className='fs-4'>{formatMoney(this.state.activeSlot.cash, 0)}</div>
                              <div style={{ fontSize: '14px' }}>{this.state.activeSlot.status}</div>
                            </div>

                          </div>

                        </div>
                      </div>

                    </div>
                  </div>

                  {/* Total Cash Benefits */}
                  <div className='container mb-3'>
                    <div className='rounded border border-light shadow-sm px-lg-4 px-3 pt-3 pb-0'>

                      <div className='my-3 row'>
                        <div className='col-lg-6 col-md-6 col-5 text-primary d-flex align-items-center'>Total<br />Cash Benefits</div>
                        <div className='col-lg-6 col-md-6 col-7'>
                          <span className='display-3'>
                            {formatMoney(this.state.totalCashBenefitAccrued, 0)}
                          </span>
                        </div>
                      </div>

                    </div>
                  </div>

                  {/* Benefits */}
                  <div className='container mb-3'>
                    <h4 className='ms-lg-2 ms-2'>Recent Accrued Benefits</h4>
                    {
                      this.state.recentPendingBenefits.map((benefit, index) => (

                        <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                          <div className='d-flex justify-content-between align-items-center'>
                            <div className='fw-bold lh-0 text-primary'>{benefit.achievement}</div>
                            <div style={{ fontSize: '12px' }}>{formatDate(benefit.created_at)}</div>
                          </div>

                          <div className='fs-6 lh-0 mt-3'>
                            {
                              benefit.benefit === null ? "No souvenir" : benefit.benefit
                            }
                          </div>

                          <div className='d-flex justify-content-between '>
                            <span className='fs-5'>{formatMoney(benefit.cash, 0)}</span>
                            <div>
                              {
                                benefit.status === 'pending' &&
                                <div className='' style={{ fontSize: '14px' }}>pending...</div>
                              }
                              {
                                benefit.status === 'given' &&
                                <>
                                  <div className='text-success' style={{ fontSize: '14px' }}>
                                    given <FontAwesomeIcon icon={faCheck}></FontAwesomeIcon>
                                  </div>
                                </>
                              }
                              {
                                benefit.status === 'cancelled' &&
                                <div className='text-danger' style={{ fontSize: '14px' }}>cancelled</div>
                              }
                            </div>

                          </div>
                        </div>

                      ))
                    }
                    <div className='d-flex justify-content-end'>
                      <Link to={this.context.auth.role === 'admin' ? '/admin/benefits' : '/member/benefits'} className='btn btn-primary btn-sm mt-3'>See All<FontAwesomeIcon className='ms-3 ' icon={faArrowRight}></FontAwesomeIcon></Link>
                    </div>

                  </div>

                  {/* Notifications */}
                  <div className='container mb-3'>
                    {
                      this.state.recentUnreadNotifications.length > 0 &&
                      <>
                        <h4 className='ms-lg-2 ms-2'>{this.state.recentUnreadNotifications.length} Unread Notifications</h4>
                        {
                          this.state.recentUnreadNotifications.map((notice, index) => (

                            <div key={index} className='mb-1 rounded border border-light shadow-sm px-lg-4 px-3 py-3'>
                              <div className='d-flex justify-content-between align-items-top'>
                                <div className='lh-0'>{notice.message === null ? "No message" : notice.message}</div>
                              </div>

                              <div className='mt-2' style={{ fontSize: '14px' }}>{formatDate(notice.created_at)}</div>

                            </div>
                          ))
                        }
                        <div className='d-flex justify-content-end'>
                          <Link to={this.context.auth.role === 'admin' ? '/admin/notifications' : '/member/notifications'} className='btn btn-primary btn-sm mt-3'>See All<FontAwesomeIcon className='ms-3 ' icon={faArrowRight}></FontAwesomeIcon></Link>
                        </div>
                      </>
                    }

                  </div>

                </div>
            }

          </section>
        </Layout>
      </>
    );
  }
}

export default MemberDashboard;